@media only screen and (max-width: 1000px) {
  li {
    float: center;
    width: 100%;

  }

  section {
    height: 200vh;
  }
  
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .menu {

    position: relative;
    min-height: 48px;
    max-width: 1024px;
    margin: 0 auto;
    z-index: 9;
  }
  
  .menu .main-menu {
    z-index: 2;
    top: 48px;
    position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
    margin: 0;
    padding: 0 48px;
    height: auto;
    box-sizing: border-box;
    /* overflow: hidden; */
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    -webkit-transition-delay: 1s;
    transition-delay: 1s;
    background-color: rgb(255, 255, 255);
    transition: background 0.45s linear, max-height 0.35s ease-in 200ms;
    max-height: 0;
  }

  .tm:checked ~ .main-menu {
    visibility: visible;
    max-height: 100%;
    -webkit-transition-delay: 1s;
    transition-delay: 1s;
    background: rgb(255, 255, 255);
    transition: background 0.35s linear, max-height 0.35s ease-in-out 200ms;
    height: 100%;

  }
  .menu a.logo {
    position: fixed;
    top: 0;
    left: 48px;
    width: auto;
    z-index: 999;
  }
  .menu input[type="checkbox"],
  .menu ul span.drop-icon {
    display: none;
  }
  

  
  .menu .sub-menu {
    background-color: #444;
  /* 	border-width: 1px 1px 0; */
    margin: 0 1em;
  }
  

  
  .menu li,
  [for="tm"],
  .menu a {
    color: rgb(0, 0, 0);
    overflow: hidden;
    text-align: center;


  }
  
  [for="tm"] {
    background: rgb(255, 255, 255);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 99;
    padding: 0 70px;
    line-height: 48px;
    height: 48px;
  }
  
  .menu a {
    padding: 1em 20px;

    display: inline-block;
    width: 100%;
    transition: all .125s ease-in-out;
    text-transform: uppercase;
    text-decoration: none;
    overflow: hidden;

    font-size: 20px;
  }
  
  .menu ul li:focus,
  .menu ul li:hover,
  .current-menu-item {
    color: blue;
 

  }
  
  .menu ul li:hover a {
    color: #333;
  }
  
  @media only screen and (min-width: 1024px) {
    .menu .main-menu,
    .tm:checked ~ .main-menu {
      display: block;
      opacity: 1;
      max-height: initial;
      top: 0;
